<template>
	<div id="new-detail">
		<div class="top">
			<div>
				<div class="icon icon-left" @click="onBack" v-if="back"></div>
			</div>
			<div class="flex">
				<div class="icon-wrap" @click="onRefresh">
					<div class="icon icon-refresh font-14"></div>
					<div class="font-12 font-500">刷新</div>
				</div>
				<div class="icon-wrap" @click="util.jsUrl('/plugin/pages/kefu/chat', {relation: 'member2kefu'})">
					<div class="icon icon-service font-18"></div>
					<div class="font-12 font-500">客服</div>
				</div>
			</div>
		</div>
		<div :class="{'status-title': 1, show: !showMap}" @click="onTogglePopup('log')">
			<div class="font-500 margin-10-r">订单{{order.status_cn}}</div>
			<div class="icon icon-right font-12 c-gray"></div>
		</div>
		<public-footer :preLoading="showPreLoading" :menufooter="menufooter" ></public-footer>
		<div class="content font-14" >
			<div :class="{inner: 1, sticky: !showMap}" id="scrollDiv" ref="scrollDiv">

				<div class="block" v-if="0">
					<van-row class="flex">
						<van-col span="2">
							<span class="icon icon-notification font-14"></span>
						</van-col>
						<van-col span="22">
							<div class="w-100 ellipsis font-14">商家尚未营业，订单会在商家营业后开始处理商家尚未营业，订单会在商家营业后开始处理</div>
						</van-col>
					</van-row>
				</div>
				<div class="block order-status">
					<div class="padding-15-b" @click="onTogglePopup('log')">
						<div class="font-14 font-500">订单{{order.status_cn}}</div>
						<div class="font-12 c-gray margin-5-t" v-if="log && order.delivery_status != 7">{{log.note}}</div>
					</div>
					<div class="btn-group van-hairline--top">
						<template v-if="order.is_pay == 0 && order.status < 5">
							<div class="btn-item active" @click="onFinishMealPay" v-if="order.order_type == 3 && order.pay_type == 'finishMeal'">
								<div class="icon icon-moneybagfill"></div>
								<div class="margin-5-t">立即支付</div>
							</div>
							<div class="btn-item active" @click="util.jsUrl('/pages/public/pay', {order_id: order.id, order_type: 'takeout'})" v-else>
								<div class="icon icon-moneybagfill"></div>
								<div class="margin-5-t">立即支付</div>
							</div>
							<div class="btn-item" @click="util.jsUrl('/tangshi/pages/table/goods', {order_id: order.id, table_id: order.table_id, sid: order.sid})" v-if="order.order_type == 3">
								<div class="icon icon-add2"></div>
								<div class="margin-5-t">加菜</div>
							</div>
							<div class="btn-item" @click="util.jsUrl('/pages/order/op', {id: order.id})" v-if="order.order_type == 1 && order.update_order_info_status == 1">
								<div class="icon icon-infofill"></div>
								<div class="margin-5-t">修改备注</div>
							</div>
						</template>
						<template v-if="order.status == 1">
							<div class="btn-item active" @click="onChangeStatus(order.id, 'cancel')">
								<div class="icon icon-roundclosefill"></div>
								<div class="margin-5-t">取消订单</div>
							</div>
							<div class="btn-item" @click="onChangeStatus(order.id, 'remind')" v-if="order.is_pay == 1">
								<div class="icon icon-timefill"></div>
								<div class="margin-5-t">催单</div>
							</div>
						</template>
						<template v-else-if="order.status > 1 && order.status < 5">
							<div class="btn-item active" @click="onChangeStatus(order.id, 'cancel')" v-if="order.customer_cancel_status">
								<div class="icon icon-roundclosefill"></div>
								<div class="margin-5-t">取消订单</div>
							</div>
							<div class="btn-item" @click="onChangeStatus(order.id, 'end_1')" v-if="order.order_type == 1 && order.status == 4">
								<div class="icon icon-countdownfill"></div>
								<div class="margin-5-t">确认送达</div>
							</div>
							<div class="btn-item" @click="onChangeStatus(order.id, 'end_2')" v-else-if="order.order_type == 2">
								<div class="icon icon-squarecheckfill"></div>
								<div class="margin-5-t">我已提货</div>
							</div>
							<div class="btn-item" @click="onChangeStatus(order.id, 'remind')" v-if="order.is_pay == 1">
								<div class="icon icon-timefill"></div>
								<div class="margin-5-t">催单</div>
							</div>
							<div class="btn-item" @click="util.jsUrl('/tangshi/pages/table/goods', {order_id: order.id, table_id: order.table_id, sid: order.sid})" v-if="order.order_type == 3 && order.is_pay == 1 && order.pay_type == 'finishMeal'">
								<div class="icon icon-add2"></div>
								<div class="margin-5-t">加菜</div>
							</div>
						</template>
						<template v-else-if="order.status == 5">
							<div class="btn-item" @click="util.jsUrl('/pages/order/award', {id: order.id})" v-if="order.show_deliveryer_award">
								<div class="icon icon-moneybagfill"></div>
								<div class="margin-5-t">打赏骑手</div>
							</div>
							<div class="btn-item active" @click="util.jsUrl('/pages/store/goods', {sid: order.sid, order_id: order.id})">
								<div class="icon icon-add2"></div>
								<div class="margin-5-t">再来一单</div>
							</div>
							<div class="btn-item" @click="util.jsUrl('/pages/order/comment', {id: order.id})" v-if="order.is_comment == 0">
								<div class="icon icon-commentfill"></div>
								<div class="margin-5-t">{{order.comment_cn}}</div>
							</div>
						</template>
						<template v-else-if="order.status == 6">
							<div class="btn-item active" @click="util.jsUrl('/pages/store/goods', {sid: order.sid, order_id: order.id})">
								<div class="icon icon-add2"></div>
								<div class="margin-5-t">再来一单</div>
							</div>
						</template>
						<template v-if="order.update_address_status == 1">
							<div class="btn-item" @click="util.jsUrl('/pages/order/address', {id: order.id})">
								<div class="icon icon-friendaddfill"></div>
								<div class="margin-5-t">修改地址</div>
							</div>
						</template>
						<template v-if="order.data && order.data.pindan">
							<div class="btn-item" @click="util.jsUrl('/pages/order/pindanbill', {id: order.id})">
								<div class="icon icon-friendfill"></div>
								<div class="margin-5-t">拼单分账</div>
							</div>
						</template>
						<div class="btn-item active" @click="onDeleteOrder" v-if="can_delete == 1" >
							<div class="icon icon-deletefill"></div>
							<div class="margin-5-t">删除订单</div>
						</div>
						<div class="btn-item" @click="util.jsUrl('/pages/order/refund', {id: order.id})" v-if="order.refund_status > 0">
							<div class="icon icon-money"></div>
							<div class="margin-5-t">退款详情</div>
						</div>
					</div>
				</div>
				<div class="swiper-container" v-if="slides">
					<van-swipe :autoplay="3000" indicator-color="#ff2d4b">
						<van-swipe-item v-for="(item, index) in slides" :key="index">
							<div @click="util.jsUrl(item.wxapp_link)">
								<img :src="item.thumb">
							</div>
						</van-swipe-item>
					</van-swipe>
				</div>
				<div class="block">
					<div class="store padding-15-b van-hairline--bottom">
						<span class="store-title">{{store.title}}</span>
						<span class="icon icon-right font-12 c-gray margin-5-l"></span>
					</div>
					<div class="goods">
						<van-row class="goods-item margin-15-tb" v-for="(item, index) in goods" :key="index">
							<van-col span="3">
								<div class="thumb">
									<img :src="item.thumb" alt="">
								</div>
							</van-col>
							<van-col span="16" class="padding-10-l">
								<div class="w-100 ellipsis">{{item.goods_title}}</div>
								<div class="goods-num">x{{item.goods_num}}</div>
							</van-col>
							<van-col span="5" class="text-right">
								<span class="font-12">{{Lang.dollarSign}}</span>{{item.goods_price}}
							</van-col>
						</van-row>
					</div>
					<div class="cost van-hairline--top padding-15-b">
						<van-row class="cost-item" v-if="order.box_price > 0">
							<van-col span="16">{{order.order_type == 3 ? '餐具费' : store.cn.box_price}}</van-col>
							<van-col span="8" class="text-right"><span class="font-12">{{Lang.dollarSign}}</span>{{order.box_price}}</van-col>
						</van-row>
						<van-row class="cost-item">
							<van-col span="16">{{store.cn.pack_fee}}</van-col>
							<van-col span="8" class="text-right"><span class="font-12">{{Lang.dollarSign}}</span>{{order.pack_fee}}</van-col>
						</van-row>
						<van-row class="cost-item" v-if="order.person_num > 0">
							<van-col span="16">餐具数量</van-col>
							<van-col span="8" class="text-right">{{order.person_num}}套</van-col>
						</van-row>
						<van-row class="cost-item">
							<van-col span="16">配送费</van-col>
							<van-col span="8" class="text-right"><span class="font-12">{{Lang.dollarSign}}</span>{{order.delivery_fee}}</van-col>
						</van-row>
						<van-row class="cost-item" v-if="order.order_type == 3">
							<van-col span="16">服务费</van-col>
							<van-col span="8" class="text-right"><span class="font-12">{{Lang.dollarSign}}</span>{{order.serve_fee}}</van-col>
						</van-row>
						<template v-if="order.data">
							<van-row v-if="order.data.extra_fee.length > 0" class="cost-item" v-for="(item, index) in order.data.extra_fee" :key="index">
								<van-col span="16">{{item.name}}</van-col>
								<van-col span="8" class=" text-right "><span class="font-12">{{Lang.dollarSign}}</span>{{item.fee}}</van-col>
							</van-row>
							<van-row v-if="order.data.store_extra_fee.length > 0" class="cost-item" v-for="(itemDiy, indexDiy) in order.data.store_extra_fee" :key="'diyFee' + indexDiy">
								<van-col span="16">{{itemDiy.name}}</van-col>
								<van-col span="8" class=" text-right "><span class="font-12">{{Lang.dollarSign}}</span>{{itemDiy.fee}}</van-col>
							</van-row>
						</template>
					</div>
					<div class="activity van-hairline--top padding-15-b" v-if="activityed.length > 0">
						<van-row class="activity-item" v-for="(item, index) in activityed" :key="index">
							<van-col span="2" class="icon-before">
								<div class="activity-label">
									<img :src="'static/img/' + item.type + '_b.png'" alt="">
								</div>
							</van-col>
							<van-col span="17">{{item.name}}</van-col>
							<van-col span="5" class="text-right discount-note">{{item.note}}</van-col>
						</van-row>
					</div>
					<div class="fee van-hairline--top">
						<div class="c-gray">订单</div>
						<div>
							<span class="font-12">{{Lang.dollarSign}}</span><span>{{order.total_fee}}</span>
						</div>
						<div class="c-gray margin-10-l">优惠</div>
						<div>
							<span class="font-12">{{Lang.dollarSign}}</span><span>{{order.discount_fee}}</span>
						</div>
						<div class="c-gray margin-10-l">合计</div>
						<div>
							<span class="font-12">{{Lang.dollarSign}}</span><span>{{order.final_fee}}</span>
						</div>
					</div>
					<div class="link van-hairline--top">
						<router-link class="link-item van-hairline--right" tag="div" :to="util.getUrl({path: '/plugin/pages/kefu/chat', query: {relation: 'member2clerk', kefuopenid: order.sid, kefuunionid: order.sid, orderid: order.id}})" v-if="order.kefu && order.kefu.store_status == 1">
							<span class="icon icon-wang"></span>
							<span>联系商家</span>
						</router-link>
						<div class="link-item " @click="onCallStoreOrDeliveryer('store')">
							<span class="icon icon-telephone"></span>
							<span>致电商家</span>
						</div>
					</div>
				</div>
				<div class="block" v-if="order.data && order.data.meal_redpacket">
					<div class="block-title van-hairline--bottom">套餐红包</div>
					<div class="info">
						<van-row class="info-item">
							<van-col span="6">购买费用</van-col>
							<van-col span="18" class="text-right">
								<span class="font-12">{{Lang.dollarSign}}</span>{{order.data.meal_redpacket.fee}}
							</van-col>
						</van-row>
					</div>
				</div>
				<div class="block" v-if="order.order_type == 2">
					<div class="block-title van-hairline--bottom">订单核销</div>
					<div class="info">
						<div>
							<div id="qrcode" class="qrcode"></div>
							<p class="text-center font-12 margin-10-t">核销码: {{order.code}}</p>
						</div>
						<div class="text-center c-gray margin-10-t">请商家扫描二维码或者填写核销码即可消费</div>
					</div>
				</div>
				<div class="block" v-if="order.order_type <= 2">
					<div class="block-title van-hairline--bottom">配送信息</div>
					<div class="info padding-15-b">
						<template v-if="order.order_type == 1 && order.deliveryer_id > 0">
							<van-row class="info-item">
								<van-col span="6">配送服务</van-col>
								<van-col span="18" class="text-right">{{order.delivery_title}}</van-col>
							</van-row>
							<van-row class="info-item">
								<van-col span="6">配送骑手</van-col>
								<van-col span="18" class="text-right">{{deliveryer.title}}</van-col>
							</van-row>
						</template>
						<van-row class="info-item">
							<van-col span="6">{{order.order_type == 1 ? '期望时间' : '自提时间'}}</van-col>
							<van-col span="18" class="text-right">{{order.delivery_day}}~{{order.delivery_time}}</van-col>
						</van-row>
						<template v-if="order.order_type == 1">
							<van-row class="info-item">
								<van-col span="6">配送地址</van-col>
								<van-col span="18" class="text-right">
									<div class="margin-5-b">{{order.username}} ({{order.sex}}) {{order.mobile}}</div>
									{{order.address}}
								</van-col>
							</van-row>
						</template>
						<template v-else-if="order.order_type == 2">
							<van-row class="info-item">
								<van-col span="6">自提地址</van-col>
								<van-col span="18" class="text-right">{{store.address}}</van-col>
							</van-row>
						</template>
						<van-row class="info-item" v-if="order.zhunshibao_status > 0">
							<van-col span="6">准时宝</van-col>
							<van-col span="18" class="text-right">
								<router-link tag='div' :to="util.getUrl({path: 'pages/order/zhunshibao', query: {id: order.id}})" class="flex">
									准时宝详情
									<div class="icon icon-right c-gray margin-5-l font-12"></div>
								</router-link>
							</van-col>
						</van-row>
					</div>
					<div class="link van-hairline--top"  v-if="order.deliveryer_id > 0">
						<router-link class="link-item van-hairline--right" tag="div" :to="util.getUrl({path: '/plugin/pages/kefu/chat', query: {relation: 'member2deliveryer', kefuopenid: deliveryer.token, orderid: order.id}})" v-if="order.kefu && order.kefu.deliveryer_status == 1 && deliveryer.kefu_status != 3">
							<span class="icon icon-wang"></span>
							<span>联系骑手</span>
						</router-link>
						<div class="link-item " @click="onCallStoreOrDeliveryer('store')">
							<span class="icon icon-telephone"></span>
							<span>致电骑手</span>
						</div>
					</div>
				</div>

				<div class="block">
					<div class="block-title van-hairline--bottom">订单信息</div>
					<div class="info">
						<van-row class="info-item">
							<van-col span="6">商户订单号</van-col>
							<van-col span="18" class="text-right">#{{order.serial_sn}}</van-col>
						</van-row>
						<van-row class="info-item">
							<van-col span="6">平台订单号</van-col>
							<van-col span="18" class="text-right">{{order.ordersn}}</van-col>
						</van-row>
						<van-row class="info-item" v-if="order.show_code == 1">
							<van-col span="6">取餐码</van-col>
							<van-col span="18" class="text-right">{{order.code}}</van-col>
						</van-row>
						<van-row class="info-item">
							<van-col span="6">订单类型</van-col>
							<van-col span="18" class="text-right">
								<template v-if="order.order_type == 1">外卖</template>
								<template v-else-if="order.order_type == 2">自提</template>
								<template v-else-if="order.order_type == 3">店内</template>
								<template v-else-if="order.order_type == 4">预定</template>
							</van-col>
						</van-row>
						<template v-if="order.order_type == 3">
							<van-row class="info-item">
								<van-col span="6">桌台号</van-col>
								<van-col span="18" class="text-right">{{order.table.title}}</van-col>
							</van-row>
							<van-row class="info-item">
								<van-col span="6">来客人数</van-col>
								<van-col span="18" class="text-right">
									{{order.person_num > 0 ? order.person_num : '未知'}}
								</van-col>
							</van-row>
						</template>
						<template v-if="order.order_type == 4">
							<van-row class="info-item">
								<van-col span="6">预定时间</van-col>
								<van-col span="18" class="text-right">{{order.reserve_time}}</van-col>
							</van-row>
							<van-row class="info-item">
								<van-col span="6">桌台类型</van-col>
								<van-col span="18" class="text-right">{{order.table_category.title}}</van-col>
							</van-row>
							<van-row class="info-item" v-if="order.table_id > 0">
								<van-col span="6">预定桌号</van-col>
								<van-col span="18" class="text-right">{{order.table.title}}</van-col>
							</van-row>
							<van-row class="info-item">
								<van-col span="6">预定类型</van-col>
								<van-col span="18" class="text-right">{{order.reserve_type_cn}}</van-col>
							</van-row>
							<van-row class="info-item">
								<van-col span="6">来客人数</van-col>
								<van-col span="18" class="text-right">{{order.person_num > 0 ? order.person_num : '未知'}}</van-col>
							</van-row>
						</template>
						<van-row class="info-item">
							<van-col span="6">支付方式</van-col>
							<van-col span="18" class="text-right">{{order.pay_type_cn}}</van-col>
						</van-row>
						<van-row class="info-item">
							<van-col span="6">备注信息</van-col>
							<van-col span="18" class="text-right">{{order.note ? order.note : '无'}}</van-col>
						</van-row>
						<template v-if="order.invoice">
							<van-row class="info-item" v-if="order.invoice.title">
								<van-col span="6">发票抬头</van-col>
								<van-col span="18" class="text-right">{{order.invoice.title}}</van-col>
							</van-row>
							<van-row class="info-item" v-if="order.invoice.recognition">
								<van-col span="6">纳税人识别号</van-col>
								<van-col span="18" class="text-right">{{order.invoice.recognition}}</van-col>
							</van-row>
						</template>
					</div>
				</div>
			</div>
			<div class="map-wrap" :class="{'map-wrap-hide': !showMap}" v-if="show_location == 1">
				<div id="map-container"></div>
			</div>
		</div>
		<template v-if="share.info.share_button > 0">
			<div class="send-redpacket" @click="orderShare = true">
				<div class="share-inner">
					<div class="icon icon-share"></div>
				</div>
			</div>
			<van-popup v-model="orderShare" :overlay="true" class="popup-send-redpacket padding-15-b">
				<div class="popup-content grant-share">
					<p class="font-20 grant-share-title margin-10-b">分享规则</p>
					<p class="font-15">1. 顾客在订单完成后{{share.share_grant_days_limit}}天之内，对订单进行评价并分享到朋友圈即可获取{{share.share_grant}}{{share.grantType_cn}}</p>
					<p class="font-15 margin-10-t">2. 每人通过分享订单最多可获取{{share.share_grant_max}}{{share.grantType_cn}}，超出后将不再奖励</p>
				</div>
				<div class="popup-footer">
					<div class="grant-share-btn">
						<router-link tag="div" :to="util.getUrl({path: 'pages/order/comment', query: {id: order.id}})" class="btn bg-danger" v-if="share.info.share_button == 1">现在去评价</router-link>
						<div class="btn bg-danger" @click="onChangeZhezhao(true)" v-else>您已评价,去分享</div>
					</div>
				</div>
			</van-popup>
		</template>
		<template v-if="share.info.superRedpacket_share_status == 1">
			<div class="send-redpacket" @click="sendRedpacket = true">
				<img src="static/img/send-redpacket.png" alt="">
			</div>
			<van-popup v-model="sendRedpacket" :overlay="true" class="popup-send-redpacket">
				<div class="popup-content border-1px-b">
					<img src="static/img/wv.png" alt="">
					<p class="font-15 font-bold">恭喜获得{{share.info.superRedpacket.packet_total}}个红包</p>
					<p class="font-14 margin-10-t">分享给小伙伴，大家一起抢。</p>
				</div>
				<div class="popup-footer flex-lr">
					<div class="cancle border-1px-r" @click="sendRedpacket = false">取消</div>
					<div class="grant" @click="onChangeZhezhao(true)">发红包</div>
				</div>
			</van-popup>
		</template>
		<div class="share-zhezhao" @click="onChangeZhezhao()" v-if="zhezhaoShow">
			<img src="static/img/share-layer.png" alt="">
		</div>
		<van-popup  class="call-phone" v-model="showSelectCall">
			<div class="call-phone-header">请选择联系人</div>
			<div class="call-plateform" @click="onSelectCall(config_mall.mobile)">
				<div>联系平台(推荐)</div>
			</div>
			<div class="call-deliveryer van-hairline--top" @click="onSelectCall(deliveryer.mobile)">
				<div>联系配送员</div>
			</div>
		</van-popup>
		<van-popup v-model="showYinsihao" class="yinsihao-popup">
			<div class="popup-title">
				<img src="static/img/yinsihao_bg.png" class="yinsihao-img" alt="">
			</div>
			<div class="popup-container van-hairline--bottom">
				<div class="font-14">将通过转播的方式保护您的隐私</div>
				<div class="yinsihao-mobile">{{yinsihao.secret_mobile}} 转 <span class="c-danger">{{yinsihao.extension}}</span></div>
				<div class="yinsihao-tips">拨打{{yinsihao.secret_mobile}}<br>接通后输入分机号{{yinsihao.extension}}#即可</div>
			</div>
			<div class="popup-footer">
				<div class="yinsihao-cancel" @click="showYinsihao = false">取消</div>
				<div class="yinsihao-confirm van-hairline--left" @click="util.jsTel(yinsihao.secret_mobile); showYinsihao = false">立即呼叫</div>
			</div>
		</van-popup>
		<van-popup v-model="servicePopupStatus" class="yinsihao-popup">
			<div class="popup-title font-500">
				订单已不能使用隐私号联系
			</div>
			<div class="popup-container padding-15-b van-hairline--bottom">
				为您联系客服
			</div>
			<div class="popup-footer">
				<div class="yinsihao-cancel" @click="servicePopupStatus = false">取消</div>
				<div class="yinsihao-confirm van-hairline--left" @click="util.jsTel(config_mall.mobile); servicePopupStatus = false">继续呼叫</div>
			</div>
		</van-popup>
		<van-popup v-model="popup.log" class="popup-log" position="bottom">
			<div class="popup-title font-500 van-hairline--bottom">
				订单跟踪
			</div>
			<div class="popup-container">
				<van-steps direction="vertical" active-icon="roundcheckfill" :active="maxid" active-color="#ff2d4b">
					<van-step v-for="(item, index) in logs" :key="index">
						<div class="flex-lr">
							<div>{{item.title}}</div>
							<div class="c-gray font-12">{{item.addtime}}</div>
						</div>
						<div class="font-12 c-gray margin-5-t" v-if="item.note" v-html="item.note"></div>
					</van-step>
				</van-steps>
			</div>
		</van-popup>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header';
	import PublicFooter from '@/components/footer';
	import navigator from '@/components/navigator';
	import { Dialog } from 'vant';
	import QRcode from 'qrcodejs2';
	import {Amap} from '@/config/AMap';
	import {GMap} from '@/config/GMap';
	export default {
		components: {
			PublicHeader,
			PublicFooter,
			navigator,
			Dialog,
		},
		data() {
			return {
				showPreLoading: true,
				active_tab: 2,
				goods: [],
				store: {
					cn: {}
				},
				order: {},
				activityed: [],
				deliveryer: {},
				logs: [],
				log: {},
				maxid: '',
				slides: [],
				refundmaxid: '',
				share: {
					info: {}
				},
				refund: {},
				refund_logs: {},
				can_delete: 0,
				menufooter: {},
				sendRedpacket: false,
				zhezhaoShow: false,
				orderShare: false,
				show_location: 0,
				showSelectCall: false,
				config_mall: {
					call_deliveryer_need_select: 0,
					mobile: ''
				},
				member: {},
				scrollTop: 0,
				showMap: false,
				showYinsihao: false,
				yinsihao: {
					secret_mobile: '',
					extension: ''
				},
				servicePopupStatus: false,
				deliveryerMarkers: [],
				memberMarkers: [],
				storeMarkers: [],
				MapType: 'gaode',
				back: false,
				popup: {
					log: false
				}
			}
		},
		methods: {
			onTogglePopup(key) {
				this.popup[key] = !this.popup[key];
			},
			onBack() {
				if(!this.back) {
					return;
				}
				this.$router.back(-1);
			},
			onRefresh() {
				window.location.reload();
			},
			onChangeStatus(oid, type, extra) {
				if(type == 'cancel') {
					var params = {
						url: 'wmall/order/index/cancel?id=' + oid,
						confirm: '确定取消订单吗?',
					}
				} else if(type == 'end_1' || type == 'end_2') {
					var params = {
						url: 'wmall/order/index/end?id=' + oid,
						confirm: '你确定收到该商家的外卖?',
						successUrl: '/pages/order/comment?id=' + oid,
					}
					if(type == 'end_2') {
						params.confirm = '你确定收到该商家的外卖?';
					}
				} else if(type == 'remind') {
					var params = {
						url: 'wmall/order/index/remind?id=' + oid,
					}
				}
				this.util.jspost(params);
			},
			onFinishMealPay() {
				Dialog.confirm({
					title: '温馨提示',
					message: '您的支付方式为餐后支付，请到商家收银台付款',
				}).then(() => {
					return;
				})
			},
			onCallStoreOrDeliveryer(type) {
				var that = this;
				if(that.order.data.yinsihao_status == 1) {
					that.util.request({
						url: 'yinsihao/yinsihao',
						data: {
							order_id: that.order.id,
							type: type,
							ordersn: that.order.ordersn
						}
					}).then((res) => {
						let result = res.data.message;
						if(result.errno) {
							if(result.errno == -1000) {
								var mobile = '';
								if(type == 'store') {
									mobile = that.store.telephone;
								} else if(type == 'deliveryer') {
									mobile = that.deliveryer.mobile;
								}
								that.util.jsTel(mobile);
							} else if(result.errno == -2) {
								that.servicePopupStatus = true;
							} else {
								this.$toast(result.message)
							}
							return;
						}
						result = result.message;
						that.yinsihao.secret_mobile = result.data.secret_mobile;
						that.yinsihao.extension = result.data.extension;
						that.showYinsihao = true;
					})
				} else {
					var mobile = '';
					if(type == 'store') {
						mobile = that.store.telephone;
					} else if(type == 'deliveryer') {
						mobile = that.deliveryer.mobile;
					}
					that.util.jsTel(mobile);
				}
			},
			onLoad() {
				if(this.$route.query.id) {
					this.id = this.$route.query.id;
				} else {
					this.$toast('订单不存在或已删除！')
				}
				this.util.request({
					url: 'wmall/order/index/newDetail',
					data: {
						id: this.id,
						menufooter: 1
					}
				}).then((res) => {
					this.showPreLoading = false;
					let result = res.data.message;
					if(result.errno) {
						this.$toast(result.message)
						return;
					}
					this.goods = [...this.goods, ...result.message.goods];
					this.store = result.message.store;
					result.message.order = this.util.df(result.message.order, 2);
					this.order = result.message.order;
					this.activityed = [...this.activityed, ...result.message.activityed];
					this.deliveryer = result.message.deliveryer;
					this.logs = [...result.message.order_log.logs];
					this.log = result.message.order_log.log;
					this.maxid = result.message.order_log.maxid;
					this.slides = result.message.slides == false ? result.message.slides : [...this.slides, ...result.message.slides];
					this.share = result.message.share;
					this.refund = result.message.refund_data.refund;
					this.refund_logs = result.message.refund_data.refund_logs;
					this.refundmaxid = result.message.refund_data.refundmaxid;
					this.can_delete = result.message.can_delete;
					this.show_location = result.message.show_location;
					this.config_mall = result.message.config_mall;
					this.MapType = this.util.getStorage('MapType');
					this.menufooter = window.menufooter;
					if(this.order.order_type == 2 && result.message.qrcode) {
						this.$nextTick(function() {
							this.newQrcode(result.message.qrcode);
						})
					}
					this.member = result.message.member;
					if(this.show_location == 1) {
						this.showMap = true;
						this.loadmap();
						setInterval(() => {
							this.onRefreshMap();
						}, 10000)
						this.checkContentDivScroll();
					}
					if(this.share.info && (this.share.info.share_button > 0 || this.share.info.superRedpacket_share_status == 1)) {
						var shareData = window._share
						var that = this;
						if(that.share.info.share_button == 2) {
							shareData.success = function() {
								that.util.request({
									url: 'ordergrant/share/grant',
									data: {id: that.order.id}
								}).then((res) => {
									that.util.$toast(res.data.message.message.message, that.util.getUrl({path: 'pages/order/detail', query: {id: that.order.id}}), 1000);
								})
							}
						}
						that.wx.share(shareData);
					}
				})
			},
			onChangeZhezhao(force) {
				if(force) {
					this.orderShare = false;
					this.sendRedpacket = false;
				}
				this.zhezhaoShow = !this.zhezhaoShow;
			},
			onDeleteOrder() {
				var that = this;
				that.$dialog.confirm({
					message: '确订删除该订单吗？'
				}).then(() => {
					that.util.request({
						url: 'wmall/order/index/delete',
						data: {
							id: that.order.id
						},
					}).then((res) => {
						let result = res.data.message;
						if(result.errno) {
							this.$toast(result.message);
							return;
						}
						that.util.$toast(result.message, that.util.getUrl({path: '/pages/order/index'}), 1500, 'replace');
					})
				}).catch(() => {

				})
			},
			onSelectCall(mobile){
				if(mobile) {
					this.util.jsTel(mobile);
				}
				this.showSelectCall = !this.showSelectCall;
			},
			newQrcode(text) {
				let qrcode = new QRcode('qrcode', {
					width: 150,
					height: 150, // 高度
					text: text, // 二维码内容
					image: ''
					// render: 'canvas' // 设置渲染方式（有两种方式 table和canvas，默认是canvas）
					// background: '#f0f'
					// foreground: '#ff0'
				})
			},
			loadmap(){
				var that = this;
				if(that.MapType == 'google') {
					GMap().then(function(google) {
						that.map = new google.maps.Map(document.getElementById('map-container'), {
							center: {
								lat: parseFloat(that.order.location_x),
								lng: parseFloat(that.order.location_y)
							},
							zoom: 10,
							disableDefaultUI: true  //禁用默认UI
						});
						that.onSetMarkers();
					});
				} else {
					Amap().then(function(AMap){
						that.map = new AMap.Map('map-container', {
							resizeEnable: true,
							center: [that.order.location_y, that.order.location_x],
							zoom: 10
						});
						that.onSetMarkers();
					});
				}
			},

			onRefreshMap() {
				var that = this;
				that.util.request({
					url: 'wmall/order/index/refresh_map',
					data: {
						id: that.order.id
					},
				}).then((res) => {
					let result = res.data.message;
					if(result.errno) {
						this.$toast(result.message);
						return;
					}
					result = result.message;
					that.order = Object.assign(that.order, result.order);
					if(result.deliveryer) {
						that.deliveryer = Object.assign(that.deliveryer, result.deliveryer);
					}
					that.show_location = result.show_location;
					that.onSetMarkers();
				})
			},

			onSetMarkers() {
				var that = this;
				if(that.MapType == 'google') {
					var overlay, bounds;
					//顾客位置
					if(that.order.location_y && that.order.location_x) {
						if(that.memberOverlay) {
							that.memberOverlay.setMap(null);
						}
						USGSOverlay.prototype = new google.maps.OverlayView();
						function USGSOverlay(bounds, map) {
							this.bounds_ = bounds;
							this.map_ = map;
							this.div_ = null;
							this.setMap(map);
						}
						USGSOverlay.prototype.onRemove = function() {
							this.div_.parentNode.removeChild(this.div_);
							this.div_ = null;
						};
						USGSOverlay.prototype.onAdd = function() {
							var div = document.createElement('div');
							if(that.order.is_pay == 0) {
								div.className = 'marker-common marker-common-gmap marker-mine-nopay';
								var imgWrap = document.createElement('div');
								imgWrap.className = 'img-wrap';
								var img = document.createElement('img');
								img.src = that.member.avatar;
								img.className = 'img-100';
								imgWrap.appendChild(img);
								div.appendChild(imgWrap);
								var div1 = document.createElement('div');
								var div2 = document.createElement('div');
								div2.className = 'font-12';
								div2.innerHTML = '等待支付';
								div1.appendChild(div2);
								div.appendChild(div1);
							} else {
								div.className = 'marker-mine-route';
							}
							this.div_ = div;
							var panes = this.getPanes();
							panes.overlayLayer.appendChild(div);
						};
						USGSOverlay.prototype.draw = function() {
							var overlayProjection = this.getProjection();
							var sw = overlayProjection.fromLatLngToDivPixel(this.bounds_.getSouthWest());
							var ne = overlayProjection.fromLatLngToDivPixel(this.bounds_.getNorthEast());
							var div = this.div_;
							if(that.order.is_pay == 0) {
								div.style.left = sw.x - 50 + 'px';
								div.style.top = ne.y - 50 + 'px';
							} else {
								div.style.left = sw.x - 35 + 'px';
								div.style.top = ne.y - 35 + 'px';
							}
						};
						bounds = new google.maps.LatLngBounds(
							new google.maps.LatLng(parseFloat(that.order.location_x), parseFloat(that.order.location_y))
						);
						that.memberOverlay = new USGSOverlay(bounds, that.map);
					}

					// 商家位置
					if(that.order.is_pay == 1 && that.store.location_y && that.store.location_x) {
						if(that.storeOverlay) {
							that.storeOverlay.setMap(null);
						}
						USGSOverlay.prototype = new google.maps.OverlayView();
						function USGSOverlay(bounds, map) {
							this.bounds_ = bounds;
							this.map_ = map;
							this.div_ = null;
							this.setMap(map);
						}
						USGSOverlay.prototype.onRemove = function() {
							this.div_.parentNode.removeChild(this.div_);
							this.div_ = null;
						};
						USGSOverlay.prototype.onAdd = function() {
							var div = document.createElement('div');
							if(that.order.status == 1) {
								div.className = 'marker-common marker-common-gmap marker-store-waiting';
								var imgWrap = document.createElement('div');
								imgWrap.className = 'img-wrap';
								var img = document.createElement('img');
								img.src = that.store.logo;
								img.className = 'img-100';
								imgWrap.appendChild(img);
								div.appendChild(imgWrap);
								var div1 = document.createElement('div');
								var div2 = document.createElement('div');
								div2.className = 'font-12';
								div2.innerHTML = '等待商家接单';
								div1.appendChild(div2);
								div.appendChild(div1);
								offsetX = -62.5;
								offsetY = -44;
							} else {
								div.className = 'marker-start-head-route';
								var img = document.createElement('img');
								img.src = that.store.logo;
								div.appendChild(img);
							}
							this.div_ = div;
							var panes = this.getPanes();
							panes.overlayLayer.appendChild(div);
						};
						USGSOverlay.prototype.draw = function() {
							var overlayProjection = this.getProjection();
							var sw = overlayProjection.fromLatLngToDivPixel(this.bounds_.getSouthWest());
							var ne = overlayProjection.fromLatLngToDivPixel(this.bounds_.getNorthEast());
							var div = this.div_;
							if(that.order.status == 1) {
								div.style.left = sw.x - 62.5 + 'px';
								div.style.top = ne.y - 115 + 'px';
							} else {
								div.style.left = sw.x - 35 + 'px';
								div.style.top = ne.y - 140 + 'px';
							}
						};
						bounds = new google.maps.LatLngBounds(
							new google.maps.LatLng(parseFloat(that.store.location_x), parseFloat(that.store.location_y))
						);
						that.storeOverlay = new USGSOverlay(bounds, that.map);
					}

					// 配送员位置
					if(that.order.deliveryer_id > 0 && that.deliveryer && that.deliveryer.location_x && that.deliveryer.location_y) {
						if(that.deliveryerOverlay) {
							that.deliveryerOverlay.setMap(null);
						}
						USGSOverlay.prototype = new google.maps.OverlayView();
						function USGSOverlay(bounds, map) {
							this.bounds_ = bounds;
							this.map_ = map;
							this.div_ = null;
							this.setMap(map);
						}
						USGSOverlay.prototype.onRemove = function() {
							this.div_.parentNode.removeChild(this.div_);
							this.div_ = null;
						};
						USGSOverlay.prototype.onAdd = function() {
							var div = document.createElement('div');
							div.className = 'marker-common marker-common-gmap marker-deliveryer-take';
							var imgWrap = document.createElement('div');
							imgWrap.className = 'img-wrap';
							var img = document.createElement('img');
							img.src = that.deliveryer.avatar;
							img.className = 'img-100';
							imgWrap.appendChild(img);
							div.appendChild(imgWrap);
							var div1 = document.createElement('div');
							var div2 = document.createElement('div');
							div2.className = 'font-12';
							div2.innerHTML = that.order.delivery_status == 7 ? '骑手正在赶往商家' : '骑手正在送餐中';
							div1.appendChild(div2);
							div.appendChild(div1);
							this.div_ = div;
							var panes = this.getPanes();
							panes.overlayLayer.appendChild(div);
						};
						USGSOverlay.prototype.draw = function() {
							var overlayProjection = this.getProjection();
							var sw = overlayProjection.fromLatLngToDivPixel(this.bounds_.getSouthWest());
							var ne = overlayProjection.fromLatLngToDivPixel(this.bounds_.getNorthEast());
							var div = this.div_;
							div.style.left = sw.x - 85 + 'px';
							div.style.top = ne.y - 190 + 'px';
						};
						bounds = new google.maps.LatLngBounds(
							new google.maps.LatLng(parseFloat(that.deliveryer.location_x), parseFloat(that.deliveryer.location_y))
						);
						that.deliveryerOverlay = new USGSOverlay(bounds, that.map);
					}
				} else {
					var content = ''
					var offsetX = '';
					var offsetY = '';
					// 顾客位置
					if(that.order.location_y && that.order.location_x) {
						that.map.remove(that.memberMarkers);
						content = '<div class="marker-mine-route"></div>';
						offsetX = -35;
						offsetY = -35;
						if(that.order.is_pay == 0) {
							content = '<div class="marker-common marker-mine-nopay">' +
								'<div class="img-wrap">' +
								'<img class="img-100" src="' + that.member.avatar +'" alt="">' +
								'</div>' +
								'<div class="">' +
								'<div class="font-12 ">等待支付</div>' +
								'</div>'
							'</div>';
							offsetX = -50;
							offsetY = -44;
						}
						var marker = new AMap.Marker({
							position: [that.order.location_y, that.order.location_x],
							offset: new AMap.Pixel(offsetX, offsetY),
							content: content
						});
						that.memberMarkers.push(marker);
						marker.setMap(that.map);
					}
					// 商家位置
					if(that.order.is_pay == 1 && that.store.location_y && that.store.location_x) {
						that.map.remove(that.storeMarkers);
						content = '<div class="marker-start-head-route"><img src="' + that.store.logo + '" alt=""/></div>';
						offsetX = -33;
						offsetY = -80;
						if(that.order.status == 1) {
							content = '<div class="marker-common marker-store-waiting">' +
								'<div class="img-wrap">' +
								'<img class="img-100" src="' + that.store.logo +'" alt="">' +
								'</div>' +
								'<div class="">' +
								'<div class="font-12 ">等待商家接单</div>' +
								'</div>'
							'</div>';
							offsetX = -62.5;
							offsetY = -44;
						}
						var marker = new AMap.Marker({
							position: [that.store.location_y, that.store.location_x],
							offset: new AMap.Pixel(offsetX, offsetY),
							content: content,
						});
						that.storeMarkers.push(marker);
						marker.setMap(that.map);
					}
					// 配送员位置
					if(that.order.deliveryer_id > 0 && that.deliveryer && that.deliveryer.location_x && that.deliveryer.location_y) {
						that.map.remove(that.deliveryerMarkers);
						if(that.order.delivery_status == 7) {
							//待取货
							content = '<div class="marker-common marker-deliveryer-take">' +
								'<div class="img-wrap">' +
								'<img class="img-100" src="' + that.deliveryer.avatar +'" alt="">' +
								'</div>' +
								'<div class="">' +
								'<div class="font-12 ">骑手正在赶往商家</div>' +
								'<div class="font-12 hide">距商家 <span class="c-danger">1.6km 18分钟</span></div> ' +
								'</div>'
							'</div>';
							offsetX = -85;
							offsetY = -44;
						} else if(that.order.delivery_status == 4) {
							//配送中
							content = '<div class="marker-common marker-deliveryer-take">' +
								'<div class="img-wrap">' +
								'<img class="img-100" src="' + that.deliveryer.avatar +'" alt="">' +
								'</div>' +
								'<div class="">' +
								'<div class="font-12 ">骑手正在送餐中</div>' +
								'<div class="font-12 hide">预计<span class="c-danger">18</span>分钟送达</span></div> ' +
								'</div>'
							'</div>';
							offsetX = -85;
							offsetY = -44;
						}
						var marker = new AMap.Marker({
							position: [that.deliveryer.location_y, that.deliveryer.location_x],
							offset: new AMap.Pixel(offsetX, offsetY),
							content: content
						});
						that.deliveryerMarkers.push(marker);
						marker.setMap(that.map);
					}
					that.map.setFitView();
				}
			},

			checkContentDivScroll() {
				var that = this;
				//获取节点
				var scrollDiv = document.getElementById('scrollDiv');
				//绑定事件
				scrollDiv.addEventListener('scroll', function() {
					var value = scrollDiv.scrollTop - that.scrollTop;
					that.scrollTop = scrollDiv.scrollTop;
					if(value < 0) {
						if(that.scrollTop <= 0 && !that.showMap) {
							that.showMap = true;
						}
					} else {
						if(that.scrollTop > 0 && that.showMap) {
							that.showMap = false;
							that.scrollTop = 0;
							that.$nextTicket(() => {
								that.$refs.scrollDiv.scrollTop = 0;
								scrollDiv.scrollTop = 0;
							})
						}
					}
				});
			},
		},
		computed: {
			offsetTop() {
				var theme = this.util.getStorage('theme');
				var offsetTop = 46;
				if(theme.header) {
					if(theme.header.ishide == 1) {
						offsetTop = 0;
					}
				}
				return offsetTop;
			}
		},
		mounted() {
			if(window.history.length > 1) {
				this.back = true;
			}
			this.onLoad();
		}
	}
</script>

